* {
  display: flex-wrap;
  line-height: 1.05;
  padding: 0;
  margin: 0;
}

b {
  padding: 0;
  margin: 0;
}

input {
  border: none;
  background-color: rgb(235, 245, 255);
  padding: 0.2rem;
}

textarea {
  width: 100%;
  max-width: 800px;
}

.header {
  display: flex;
  justify-content: space-between;
}

.pl {
  padding-left: 0.2rem;
}

.pr {
  padding-right: 0.2rem;
}

.btn {
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  border-right: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  padding: 0 0.1rem;
  margin: 0 0.1rem;
}

/* option {
  padding: 0.1rem;
  text-align: center;
  text-justify: center;
} */

/* select {
  width: 100%;
  padding: 0.1rem;
  text-align: center;
  text-justify: center;
} */

.bdr {
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  border-right: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  padding: 0.2rem;
  margin: 0.2rem;
}

.m {
  margin: 0.2rem;
}

.p {
  padding: 0.2rem;
}

.c {
  text-align: center;
  text-justify: center;
  justify-content: center;
}

.group-list-item-container {
  flex-wrap: wrap;
  min-width: 100px;
  max-width: 48%;
  height: 100%;
  flex-grow: 1;
}

.selected {
  transition: background-color 1s;
  background-color: rgba(230, 230, 240, 1);
}

.unselected {
  transition: background-color 1s;
  background-color: rgba(0, 0, 0, 0);
}

.single-item-container {
  flex-wrap: wrap;
  min-width: 100px;
  max-width: 500px;
  margin: 0.1rem;
  padding: 0 0.2rem;
  /* border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-left: 1px solid rgba(0, 0, 0, 0.1); */
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  border-right: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  flex-grow: 1;
}

.fwmp {
  display: flex;
  flex-wrap: wrap;
  align-items: space-between;
  justify-content: space-around;
}

.fwmp > * {
  box-sizing: content-box;
  flex-grow: 1;
  margin: 0.1rem;
  /* padding: 0.1rem; */
  /* border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-left: 1px solid rgba(0, 0, 0, 0.1); */
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  border-right: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 5px;
}

.fwmp > *:hover {
  border-bottom: 1px solid rgba(0, 0, 0, 0.6);
  border-right: 1px solid rgba(0, 0, 0, 0.6);
  /* background-color: tan; */
}

.group-item-label {
  box-sizing: content-box;
  flex-grow: 1;

  /* margin: 0.1rem; */
  /* padding: 0.1rem; */
}

.group-item {
  box-sizing: content-box;
  flex-grow: 1;
  margin: 0.1rem;
  padding: 0.1rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  border-right: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  cursor: pointer;
}

.group-item:hover {
  border-bottom: 1px solid rgba(0, 0, 0, 0.6);
  border-right: 1px solid rgba(0, 0, 0, 0.6);
}

.clickable {
  cursor: pointer;
}

.App {
  font-family: sans-serif;
  padding: 0.1rem;
}

li {
  display: flex-wrap;
}
ul {
  display: flex-wrap;
  padding-inline-start: 1rem;
}

span {
  height: 100%;
  width: 100%;
  font-size: auto;
  padding: 0.1rem 0;
  margin: 0.1rem 0;
}

.indented {
  padding-left: 0.2rem;
}

.compact {
  display: flex;
  flex-wrap: wrap;
}

.normal {
  display: block;
}
